import React from "react"
import { useStaticQuery, graphql } from 'gatsby'
import Img from "gatsby-image"
import BackgroundImage from 'gatsby-background-image'

const MissionSection = props => {

  const data = useStaticQuery(graphql`
  query {
    missionbg: file(relativePath: {eq: "missionbg2.png"}) {
      childImageSharp {
          fluid(maxWidth: 1920) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`)

  return (

    <BackgroundImage fluid={data.missionbg.childImageSharp.fluid} backgroundColor={`#040e18`}>
    <div id="mission" class="p-5 d-flex align-items-center two-thirds-height ">
      <div class="container text-center white-container mission">
        <div class="row justify-content-center">
          <div class="col-md-9 p-5">
            <div class="text-center ">
            <h1 class="mb-0">Flmingo</h1>
            <h3>Kollektiv</h3>
            <hr class="green-divider mt-3 mb-5 "></hr>
            </div>
            <p>
              We are a collective of small organic farms in Teltow-Flming, Brandenburg. We produce vegetables free of chemicals and pesticides using permaculture principles. We make homemade products, such as jams, alcohols and pickled vegetables, provide fresh produce to local restaurants and shops, and host events ranging from farm-to-table dinners, weekend workshops, to company offsites.
            </p>
            {/*<p><b>Now go build something great.</b></p>*/}
          </div>

        </div>
      </div>
    </div>
    </BackgroundImage>
  )
}

export default MissionSection
