import React from "react"
import { useStaticQuery, graphql } from 'gatsby'
import Img from "gatsby-image"


const Events = () => {
  
  const data = useStaticQuery(graphql`
  query {
    chessWeekendImg: file(relativePath: {eq: "chessweekend.jpeg"}) {
      childImageSharp {
          fluid(maxWidth: 1000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    offsite1Img: file(relativePath: {eq: "offsite1.jpg"}) {
      childImageSharp {
          fluid(maxWidth: 1000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    offsite2Img: file(relativePath: {eq: "offsite2.jpg"}) {
      childImageSharp {
          fluid(maxWidth: 1000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    offsite3Img: file(relativePath: {eq: "offsite3.jpg"}) {
      childImageSharp {
          fluid(maxWidth: 1000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`)

  return (
    
  <div id="events" class="p-5 d-flex align-items-center full-height ">
    <div class="container text-center">
      <div class="row justify-content-center">
        <div class="col-md-6">
          <h1 class="mb-3">Events</h1>
          <hr class="green-divider mt-4 mb-5"></hr>
          <div class="event">
            <h2 class="mb-4">Farm-to-Table Popup Dinner in Berlin</h2>  
            <p class=" mb-5">
              A dinner experience from seasonal and regional products home grown and made by Flamingo Kollektiv. 
            </p>
          </div>
          <div class="event">
            <h2 class="mb-4">Chess Weekend</h2>  
            <p class=" mb-5">
            Every year Datscha Frhden organises a chess weekend for all lovers of the game. Chess lovers of any level spend time playing games in the garden, watching related movies and documents and eating home grown food. 
            </p>
            <Img className="mb-5 rounded shadow-sm" fluid={data.chessWeekendImg.childImageSharp.fluid} alt="Chess weekend" />
          </div>
          <div class="event">
            <h2 class="mb-4">Offsite Meetings</h2>  
            <p class=" mb-5">
            Looking for a quiet place outside Berlin, to gather as a team and discuss strategy and ideas? Datscha Frhden can host teams of up to 20. With a 40m2 room equipped with a projector, and various outdoor undercover spaces, we have the space for your team to unwind and recharge from city life. We can also provide healthy seasonal meals, direct from our garden.   
            </p>
            <Img className="mb-5 rounded shadow-sm" fluid={data.offsite1Img.childImageSharp.fluid} alt="Offsite 1" />
            <Img className="mb-5 rounded shadow-sm" fluid={data.offsite3Img.childImageSharp.fluid} alt="Offsite 3" />
            <Img className="mb-5 rounded shadow-sm" fluid={data.offsite2Img.childImageSharp.fluid} alt="Offsite 2" />
          </div>

        </div>
      </div>
    </div>
  </div>
  
  )
}

export default Events

