import React from "react"

import { ListGroup } from 'react-bootstrap'
import { useStaticQuery, graphql } from 'gatsby'
import Img from "gatsby-image"

const Baskets = (props) => {

  const data = useStaticQuery(graphql`
  query {
    basketImg: file(relativePath: {eq: "basket.png"}) {
      childImageSharp {
          fluid(maxWidth: 1000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`)

return (
    <div id="baskets" class="p-5 d-flex align-items-center full-height ">
      <div class="container text-center">
        <div class="row justify-content-center">
          <div class="col-md-6">
          <div class="mb-5">
            <h1 class="mb-3">Flamingo Baskets</h1>
            <hr class="green-divider mt-4 mb-5"></hr>
            <div class="col-md-6 justify-content-center" style={{margin:"auto"}}>
            <Img 
                  className="mb-4 rounded-circle shadow" 
                  fluid={data.basketImg.childImageSharp.fluid} 
                  alt="Basket" />
            </div>
            <div class="mt-5">
          <p>We provide companies Flmingo Baskets for their employees. These baskets contain a mixture of fresh produce, alcohols and pickled products, all from Teltow-Flmingo, Brandenburg. Great goodie for employer branding. </p>
          </div>
          </div>
        

        </div>
        </div>
      </div>
    </div>
  )
}

/* Image with componet:
          <div class="center-block" style={{ maxWidth: `300px`}}>
            <Image />
          </div>
          */
export default Baskets
